<template>
    <div style="text-align:left">
    <el-row>
        <el-button type="danger" size="small" :disabled="remove_status"><i class="el-icon-delete"></i> 移除节点</el-button>
        <el-button type="primary" size="small" @click="addEndpoint"><i class="el-icon-plus"></i> 新增节点</el-button>
    </el-row>
    <el-row>
      <el-table
      :data="tableData"
      :v-model="tableData"
      stripe
      highlight-current-row
      style="width: 100%"
      tooltip-effect="dark"
      @selection-change="handleSelectionChange"
      >
        <el-table-column
          type="selection"
          width="55">
        </el-table-column>
         <el-table-column prop="Name" label="节点名"></el-table-column>
        <el-table-column prop="Type" label="类型"></el-table-column>
        <el-table-column   prop="URL" label="URL"></el-table-column>
        <el-table-column  prop="Group" label="组"></el-table-column>
        <el-table-column  prop="Actions" label="权限管理"></el-table-column>
      </el-table>
    </el-row>
    
  </div>
</template>


<script>
  export default {
    
    data() {
      return {
        tableData:[],
        multipleSelection: [],
        remove_status:true,
        endpoint_Names:[],
      }
    },
    methods: {
      setTable(){
        let resdata = [
           {
            Name:'111111111',
            Type:'bisafe:4.0.0.1',
            URL:'bridge',
            Group:'20170108',
            Actions:'127.0.0.1'
          },
          {
            Name:'111111111',
            Type:'bisafe:4.0.0.1',
            URL:'bridge',
            Group:'20170108',
            Actions:'127.0.0.1'
          },
          {
            Name:'111111111',
            Type:'bisafe:4.0.0.1',
            URL:'bridge',
            Group:'20170108',
            Actions:'127.0.0.1'
          },
          {
            Name:'111111111',
            Type:'bisafe:4.0.0.1',
            URL:'bridge',
            Group:'20170108',
            Actions:'127.0.0.1'
          },
        ]
        this.tableData = resdata
      },
      handleSelectionChange(val){
        this.endpoint_Names = []
        val.forEach(item => {
          this.endpoint_Names.push(item.Name)
        })
        if(this.endpoint_Names.length !== 0){
            this.remove_status = false
        }else{
            this.remove_status = true
        }
      },

      addEndpoint(){
        this.$router.push("/home/addEndpoint")
      }
    },
    activated(){
      this.setTable()
    },
   
  }
</script>

<style>
.el-table-column{
  height: 8px;
}
 .el-row {
    margin-bottom: 20px;
  }
   .block{
    float: right;
  }
</style>


