<template>
    <div style="text-align:left">
    <el-row>
        <el-button type="danger" size="small" :disabled="remove_status"><i class="el-icon-delete"></i> 移除网络</el-button>
        <el-button type="primary" size="small" @click="createNetwork"><i class="el-icon-plus"></i> 新增网络</el-button>
    </el-row>
    <el-row>
      <el-table
      :row-key="getRowKey"
      ref=multipleTable
      :data="tableData"
      :v-model="tableData"
      stripe
      highlight-current-row
      style="width: 100%"
      tooltip-effect="dark"
      @selection-change="handleSelectionChange"
      >
        <el-table-column
          type="selection"
          width="55"   v-bind:selectable="chkstu" :reserve-selection="reserveSelection">
        </el-table-column>
        <el-table-column prop="Id" label="网络编号"></el-table-column>
        <el-table-column prop="Name" label="网络名"></el-table-column>
        <el-table-column prop="Scope" label="作用域"></el-table-column>
        <el-table-column   prop="Driver" label="模型"></el-table-column>
      </el-table>
    </el-row>
    
  </div>
</template>


<script>
  export default {
    
    data() {
      return {
        loading:true,
        tableData:[],
        multipleSelection: [],
        remove_status:true,
        networks_Names:[],
        reserveSelection:true
      }
    },
    methods: {
       async setTable(){
          this.loading = true
          this.tableData = await  this.$api.network.getNetworks()
          this.loading = false
      },
      getRowKey(row){
        return row.id
      },
      handleSelectionChange(val){
        this.networks_Names = []
        val.forEach(item => {
          this.networks_Names.push(item.Name)
        })
        if(this.networks_Names.length !== 0){
            this.remove_status = false
        }else{
            this.remove_status = true
        }
      },
      chkstu(row, index){
          if(row.Driver == "bridge" || row.Driver == "none" || row.Driver == "host"){
              return false
          }else{
              return true
          }
      },
      createNetwork(){
        this.$router.push("/home/createNetwork")
      }
    },
    async activated(){
      await this.setTable()
    },
   
  }
</script>

<style scoped>
.el-table-column{
  height: 8px;
}
 .el-row {
    margin-bottom: 20px;
  }
   .block{
    float: right;
  }
</style>


