import http from './http'
export default {
//查询所有镜像
getImages: async () => {
    let tableData = []
    await http.get("/api/images/json").then(res =>{
        res.forEach(item =>{
            let resdata = {}
            resdata.Id = item.Id.substring(0,12)
            resdata.Tags = item.RepoTags
            resdata.Size = item.Size
            resdata.Created = item.Created
            tableData.push(resdata)
        })
    })
    return tableData
},
//导出镜像
exportImage: async () => {
    let ret = false
    let url = "/api/images/"+ name +"/get"
    await http.get(url).then(res => {
        ret = true
    })
    return ret
},
//也有导入多个镜像的接口
//导入镜像 Load a set of images and tags into a repository.
importImage: async () => {
    let query = {
        quiet: false
            /*boolean 
            default false 
            Suppress progress details during load.
            */
    }
    let ret = false
    await http.post("/api/images/load").then(res => {
        ret = true
    })
    return ret
},
//构建镜像
buildImage: async () => {
    await http.post("/api/build", {})
},
//创建镜像
createImage: async () => {
    let query = {
        fromImga: '', //Name of the image to pull. The name may include a tag or digest. This parameter may only be used when pulling an image. The pull is cancelled if the HTTP connection is closed
        fromSrc: '', //Source to import. The value may be a URL from which the image can be retrieved or - to read the image from the request body. This parameter may only be used when importing an image.
        repo: '', //Repository name given to an image when it is imported. The repo may include a tag. This parameter may only be used when importing an image.
        tag: '',  //Tag or digest. If empty when pulling an image, this causes all tags for the given image to be pulled
        platform: ''/*
         default: ""
         Platform in the format os[/arch[/varia
        */
    }
    await http.post("/api/images/create")
},
//查看镜像详情
inspectImage: async (name) => {
    let tableData = {}
    let url = "/api/images/"+ name +"/json"
    await http.get(url).then(res => {
        tableData = res
    })
    return tableData
},
//查看镜像历史
getHistory: async (name) => {
    let tableData = []
    let url = "/api/images/"+ name +"/history"
    await http.get(url).then(res => {
        tableData = res
    })
    return tableData
},
//拉取镜像 name: 可以是id和name
pushImage: async (name) => {
    let ret = false
    let url = "/api/images/"+ name +"/push"
    await http.post(url).then(response => {
        ret = true
    })
    return ret
},
//Tag镜像 name: Image name or ID to tag
tagImage: async (name) => {
    let query = {
        repo: '', //The repository to tag in. For example, someuser/someimage.
        tag: '', //The name of the new tag.
    }
    let ret = false
    let url = "/api/images/"+ name +"/tag"
    await http.post(url).then(response => {
        ret = true
    })
    return ret
},
//移除镜像
removeImage: async (name) => {  
    let query = {
        force: false, /*
            default false
            Remove the image even if it is being used by stopped containers or has other tags
        */
       noprune:false /*
            default false
            Do not delete untagged parent images
       */
    }
    let ret = false
    let url = "/api/images/"+ name
    await http.delete(url).then(response => {
        ret = true
    })
    return ret
},
//删除没有使用的镜像
deleteUnusedImage: async () => {
    let ret = false
    let url = "/api/images/prune"
    await http.post(url).then(response => {
        ret = true
    })
    return ret
},
//利用容器创建一个镜像
createImageByContainer: async () => {
    let query = {
        container: '',/*
                    string 
                    The ID or name of the container to commit
                    */
        repo: '',
                    /*string 
                    Repository name for the created image
                    */
        tag: '', 
                    /*string 
                    Tag name for the create image
                    */
        comment: '', 
                    /*string 
                    Commit message*/
        author: '', 
                /*string 
                Author of the image (e.g., John Hannibal Smith <hannibal@a-team.com>)*/
        pause: true,
                /*
                boolean 
                default true 
                Whether to pause the container before committing
                */
        changes : '',
                /*string 
                Dockerfile instructions to apply while committing
                */
    }
    let ret = false
    let url = "/api/commit"
    await http.post(url).then(response => {
        ret = true
    })
    return ret
}


}


