import Vue from 'vue'
import Router from 'vue-router'
import Register from '@/components/Register'
import Login from '@/components/Login'
import Home from '@/components/Home'
import ContainerList from '@/components/Container/ContainerList'
import ImageList from '@/components/Image/ImageList'
import NetworkList from '@/components/Network/NetworkList'
import Dashborad from '@/components//Dashborad/Dashborad'
import AddEndpoint from '@/components/Endpoint/AddEndpoint'
import EndpointList from '@/components/Endpoint/EndpointList'
import CreateContainer from '@/components/Container/CreateContainer'
import BuildImage from '@/components/Image/BuildImage'
import CreateNetwork from '@/components/Network/CreateNetwork'
import InspectContainer from '@/components/Container/InspectContainer'
import ContainerState from '@/components/Container/ContainerState'
import EndpointSetting from '@/components/Endpoint/EndpointSetting'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/register',
      name: 'Register',
      component: Register,
      meta:{auth:false},
    },
    {
      path: '/login',
      name: 'Login',
      component: Login,
      meta:{auth:false},
    },
    {
      path: '/home',
      name: 'Home',
      component: Home,
      meta:{auth:true},
      children: [
        {path: 'containerList',component: ContainerList},
        {path: 'imageList',component: ImageList},
        {path: 'networkList',component: NetworkList},
        {path: 'dashborad',component: Dashborad},
        {path: 'endpointList',component: EndpointList},
        {path: 'addEndpoint',component: AddEndpoint},
        {path: 'createContainer',component: CreateContainer},
        {path: 'buildImage',component: BuildImage},
        {path: 'createNetwork',component: CreateNetwork},
        {path: 'inspectContainer',component: InspectContainer},
        {path: 'containerState',component: ContainerState}
      ]
    },
    {
      path: '/',
      redirect: '/login'
    },
    {
      path: '/test',
      component: () => import('../components/test/TestTranser')
    },
    {
      path: '/serError', //服务器出错跳转页面
      component: () => import('../components/error/serError.vue')
    },
    {
      path: '/endpointSetting',
      component: EndpointSetting
    }
  ]
})




