import http from './http'
export default {
//获取节点列表
getEndpoints: async () => {
    let username = localStorage.getItem("username")
    let data = null
    await http.post('/endpointList', {email:username})
        .then(response => {data = response.endpoints})
        .catch(() => {data = null});
    return data;
},
//新增节点
addEndpoint: async (endpoint) => {
    let data = null
    console.log(endpoint)
    await http.post('/addEndpoint', endpoint)
        .then(response => {data = response})
        .catch(() => {data = null});
    return data;
},


}




