<template>
  <el-collapse v-model="activeNames" >
    <el-collapse-item name="1">
      <template slot="title" >
        <!--自定义面板-->
        <el-tag type="info">
          刷新时间
          <i class="el-icon-s-tools"></i>
        </el-tag>
      </template>
      <div class="block">
        <el-slider show-input v-model="timeValue" :max="10" :step="0.1" @change="change"></el-slider>
      </div>
    </el-collapse-item>
    <el-collapse-item name="2">
      <template slot="title">
        <!--自定义面板-->
        <el-tag>
          系统资源
          <i class="el-icon-arrow-down"></i>
        </el-tag>
      </template>
      <el-table v-loading="sysloading" :data="sysTableData" style="width: 100%">
        <el-table-column prop="Id" label="CONTAINER" ></el-table-column>
        <el-table-column prop="cpus" label="CPU %" ></el-table-column>
        <el-table-column prop="usageLimit" label="MEM USAGE / LIMIT"></el-table-column>
        <el-table-column prop="mem" label="MEM %"></el-table-column>
        <el-table-column prop="network" label="NET I/O"></el-table-column>
        <el-table-column prop="block" label="BLOCK I/O"></el-table-column>
      </el-table>
    </el-collapse-item>
    <el-collapse-item name="3">
      <template slot="title">
        <!--自定义面板-->
        <el-tag>
          进程资源
          <i class="el-icon-arrow-down"></i>
        </el-tag>
      </template>
      <el-table v-loading="proloading" :data="proTableData" style="width: 100%">
        <el-table-column prop="uid" label="UID" ></el-table-column>
        <el-table-column prop="pid" label="PID" ></el-table-column>
        <el-table-column prop="stime" label="STIME"></el-table-column>
        <el-table-column prop="time" label="TIME"></el-table-column>
        <el-table-column prop="cmd" label="CMD"></el-table-column>
      </el-table>
    </el-collapse-item>
  </el-collapse>
</template>
<script>
export default {
  data() {
    return {
      id:'', //容器id
      sysTableData: [],
      proTableData:[],
      sysloading: true,
      proloading: true,
      timeValue:3,
      intervalHandle1:null, //定时器
      intervalHandle2:null, //定时器
      activeNames: ['1','2','3'],
    };
  },
  methods: {
    
    async getContainerState() {
      let tempData = await  this.$api.container.getContainerStats(this.id)
      this.sysloading = false;
      if(tempData === null){
          this.$message({type:'error', message:'获取容器系统资源状态失败'})
          return false
      }else{
          this.sysTableData = tempData
      }
      return true;
    },
    async getTop() {
      let tempData = await  this.$api.container.getProcessById(this.id)
      this.proloading = false;
      if(tempData === null){
          this.$message({type:'error', message:'获取容器进程资源状态失败'})
          return false
      }else{
          this.proTableData = tempData
      }
      return true;
    },
    //定时刷新资源信息
    update(){
        if(this.timeoutHandle1){
            clearInterval(this.intervalHandle1)
        }
        if(this.timeoutHandle2){
            clearInterval(this.intervalHandle2)
        }
        let ret1 = false, ret2 =false
        this.intervalHandle1 = setInterval(async ()=>{ ret1 = await this.getContainerState();}, this.timeValue*1000)
        this.intervalHandle2 = setInterval(async ()=>{ ret2 = await this.getTop()}, this.timeValue*1000)
        if(ret1 === false){
           clearInterval(this.intervalHandle1)
        }
        if(ret2 === false){
            clearInterval(this.intervalHandle2)
        }
    },
     change(value){
        if(this.timeValue >= 1){
            this.update()
        }else if(this.timeValue === 0){
            this.$message({type:'info', message:"关闭刷新"})
        }else{
            this.timeValue = 0;
            this.$message({type:'info', message:"刷新时间间隔不能小于1s"})
        }
        
    },
  },
  activated() {
    this.id = this.$route.query.id
    this.sysloading = true;
    this.proloading = true;
    this.getContainerState()
    this.getTop()
    this.update()
  }
};
</script>

<style scoped>
.el-collapse-item__header {
  margin-left: 10px;
  margin-top: 50px;
}

.el-slider {
  margin-left: 15px;
}
</style>


