<template>
    <el-form ref="form" label-position="left" :model="form" label-width="80px">
        <el-form-item label="Name">
            <el-input v-model="form.name" placeholder="e.g. docker-prod01" ></el-input>
        </el-form-item>
         <el-form-item label="URL">
            <el-input v-model="form.url" placeholder="e.g. 10.0.0.10:90001"></el-input>
        </el-form-item>
        <el-form-item label="Public IP">
            <el-input v-model="form.publicIP" placeholder="e.g. 10.0.0.10 or mydocker.mydomain.com"></el-input>
        </el-form-item>
        <el-form-item label="TLS">
            <el-switch v-model="form.tls"></el-switch>
        </el-form-item>
        <div v-show="form.tls">
            <el-divider>选择TLS模式</el-divider>
            <el-button  class="bottonSize" type='primary' @click="mode = 0">同时验证服务端和客户端</el-button>
            <el-button class="bottonSize" type='primary' @click="mode = 1">仅验证客户端</el-button>
            <el-button class="bottonSize" type='primary' @click="mode = 2">仅验证服务端</el-button>
            <el-button class="bottonSize" type='primary' @click="mode = 3">服务端和客户端均不验证</el-button>
            <el-divider>上传证书文件</el-divider>
            <el-form-item label="TLS CA certificate" label-width="200px" v-show="mode === 0 || mode === 2">
                <el-upload
                class="upload-demo"
                action=""
                :on-preview="handlePreview"
                :on-remove="handleRemove"
                :before-remove="beforeRemove"
                multiple
                :on-exceed="handleExceed"
                :file-list="form.fileList">
                <el-button size="small" type="primary">选择文件</el-button>
                </el-upload>
            </el-form-item>
             <el-form-item label="TLS certificate" label-width="200px" v-show="mode === 0 || mode === 1">
                <el-upload
                class="upload-demo"
                action=""
                :on-preview="handlePreview"
                :on-remove="handleRemove"
                :before-remove="beforeRemove"
                multiple
                :on-exceed="handleExceed"
                :file-list="form.fileList">
                <el-button size="small" type="primary">选择文件</el-button>
                </el-upload>
            </el-form-item>
             <el-form-item label="TLS key" label-width="200px" v-show="mode === 0 || mode === 1">
                <el-upload
                class="upload-demo"
                action=""
                :on-preview="handlePreview"
                :on-remove="handleRemove"
                :before-remove="beforeRemove"
                multiple
                :on-exceed="handleExceed"
                :file-list="form.fileList">
                <el-button size="small" type="primary">选择文件</el-button>
                </el-upload>
            </el-form-item>
        </div>
        <el-form-item>
            <el-button type="primary" @click="onSubmit"><i class="el-icon-plus"></i> 添加节点</el-button>
        </el-form-item>
    </el-form>
</template>

<script>
export default {
    data(){
        return{
            form:{
                name:'',
                url:'',
                publicIP:'',
                tls:false
            },
            fileList:[],
            mode:0
        }
    },
    methods: {
      onSubmit() {
        console.log('submit!');
      }
    }
}
</script>

<style>
.bottonSize{
        width: 180px;
        height: 60px;
        margin-bottom: 20px;
    }
</style>
    


