  //查询所有容器
  getContainers: async () => {
    let tableData = []
    await http.get("/api/containers/json?all=true").then(res => {
      res.forEach(item => {
        let resdata = {}
        resdata.Id = item.Id.substring(0, 12)
        resdata.Name = item.Names[item.Names.length - 1].substring(1)
        resdata.State = item.State
        resdata.Image = item.Image
        resdata.Created = tool.formatDate(parseInt(item.Created) * 1000)
        for (var key in item.NetworkSettings.Networks) {
          if (item.NetworkSettings.Networks[key].IPAddress) {
            resdata.IPAddress = item.NetworkSettings.Networks[key].IPAddress
          } else {
            resdata.IPAddress = '-'
          }
        }
        tableData.push(resdata)
      })
    }).catch(error => {tableData = null})
    return tableData
  },
  //容器操作 启动start 停止stop 暂停pause 恢复 移除
  containerOpt: async (id, opt) => {
    let ret = false
    if (opt === "remove") {
      let url = "/api/containers/" + id
      await http.delete(url).then((response) => { ret = true })
    } else {
      let url = "/api/containers/" + id + '/' + opt
      await http.post(url).then(response => { ret = true }).catch(() => {ret = false})
    }
    return ret
  },
