<template>
<div class="comment">
<el-row style="border-bottom:solid 1px #e6e6e6 ">
  <el-col>
  <el-menu mode="horizontal"  style="float:right;border-bottom:0">
    <el-menu-item index="1" disabled>处理中心</el-menu-item>
    <el-menu-item index="1" disabled>我的工作台</el-menu-item>
    <el-menu-item index="3" disabled>消息中心</el-menu-item>
    <el-menu-item><a @click.prevent="loginOut">登出</a></el-menu-item>
  </el-menu>
 
</el-col>
</el-row>

<el-row class="tac"  :gutter="40">
    <el-col class="left">
      <el-menu
        :default-active="$route.path"
        class="el-menu-vertical-demo"
        @open="handleOpen"
        @close="handleClose"
        background-color="#30426a"
        text-color="#fff"
        active-background-color="#2d3e65"
        active-text-color="#fff"
        router
        >
        <el-menu-item index="/home/dashborad" >
          <span slot="title" style="margin-left:30px">仪表盘</span>
        </el-menu-item>

        <el-menu-item index="/home/containerList">
          <i class="el-icon-menu"></i>
          <span slot="title">容器管理</span>
        </el-menu-item>
        <el-menu-item index="/home/imageList">
          <i class="el-icon-document"></i>
          <span slot="title">镜像管理</span>
        </el-menu-item>
        <el-menu-item index="/home/networkList">
          <i class="el-icon-setting"></i>
          <span slot="title">网络管理</span>
        </el-menu-item>
        <el-menu-item index="/home/endpointList" disabled>
          <i class="el-icon-setting"></i>
          <span slot="title">节点管理</span>
        </el-menu-item>
        
      </el-menu>
    </el-col>

  <el-col  class="right">
    <el-scrollbar style="height:100%" ref="el-scrollbar">
      
      <keep-alive>
        <router-view></router-view>
      </keep-alive>
   </el-scrollbar>
  </el-col>
   

</el-row>

</div>
</template>

<script>
export default {
  data() {
    return {};
  },

  methods: {
    handleOpen(key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath);
    },
    goTo(path) {
      this.$router.replace(path);
    },
    loginOut(){
      this.$store.dispatch('setToken', null);
      this.$router.replace('/login')
    }
  },
};
</script>

<style>
.el-scrollbar__wrap{overflow:hidden;}
.comment{
  width:100%;
  padding:0;
  margin:0 auto;
  height: 100%;
}
.el-menu-vertical-demo{
  height: 100%;
}
.el-menu{
  text-align: left;
}
.left{
  width:20%;
  height: 100%;
}
.right{
  width: 80%;
  height: 85vh;
}
.tac{
  height: 85vh;
}
.el-scrollbar__wrap {
   overflow-y: scroll;
}
</style>


