<template>
<el-form :model="dynamicValidateForm" ref="dynamicValidateForm" label-width="100px" class="demo-dynamic">
  <!--创建容器表单 1、容器名 2、镜像选择 3、高级选项 4、支持批量创建（新增，移除按钮）-->
  <el-form-item
    v-for="(name, index) in dynamicValidateForm.names"
    :label="'容器名' + index"
    :key="name.key"
    :prop="'names.' + index + '.value'"
    :rules="rules"
  >
    <el-col :span="12"><el-input v-model="name.value"  placeholder="请输入容器名"><template slot="append">{{append.images[index].value}}</template></el-input></el-col>
    <el-col :span="2">
      <!--镜像选择对话框-->
      <el-button type="info" @click="dialogTableVisible = true">选择镜像</el-button>
      <el-dialog title="镜像列表" :visible.sync="dialogTableVisible">
        <div> 
            <div style="display: inline-block;"> 镜像名：</div>
            <el-input v-model="search" prefix-icon="el-icon-search" style="display: inline-block;width: 350px" placeholder="请输入搜索内容"></el-input>
        </div>
        <el-table :data="searchData.slice((currentPage-1)*pageSize,currentPage*pageSize)" @current-change="handleSelectionChange" >
          <el-table-column label="操作" width="150">
            <template slot-scope="scope">
　　　　　　　　<el-radio v-model="checked" :label="scope.row.Id"></el-radio> 
            </template>
          </el-table-column>
          <el-table-column property="Id" label="镜像ID" width="150"></el-table-column>
          <el-table-column property="Tags" label="镜像名" width="200"></el-table-column>
          <el-table-column property="Size" label="大小"></el-table-column>
          <el-table-column property="Created" label="创建时间"></el-table-column>
        </el-table>

        <div style="width:100%;text-align:right">
          <el-pagination
            background
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="pageSizes"
            :page-size="pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total">
          </el-pagination>
      </div>

        <div style="width:100%;text-align:right">
        <el-button type="primary" @click="addImage(index)" >确 定</el-button>
        </div>
      </el-dialog>
    </el-col>
    <!--高级选项对话框-->
     <el-col :span="2"><el-button type="info" @click="dialogFormVisible = true"><i class="el-icon-setting"></i>高级选项</el-button></el-col>
      <el-dialog title="高级设置" :visible.sync="dialogFormVisible">
        <el-form :model="settingform">
          <el-form-item label="存储设置" :label-width="formLabelWidth" >
            <el-input v-model="settingform.volume" style="width:550px"></el-input>
          </el-form-item>
          <el-form-item label="网络设置" :label-width="formLabelWidth" style="margin-top:20px" >
            <el-select v-model="settingform.network" placeholder="请选择网络" style="width:550px;">
              <el-option label="网络1" value="shanghai"></el-option>
              <el-option label="网络2" value="beijing"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false">取 消</el-button>
          <el-button type="primary" @click="dialogFormVisible = false">确 定</el-button>
        </div>
      </el-dialog>
    <!--新增和移除按钮-->
    <el-col :span="2"><el-button type=text @click.prevent="removeDomain(name)" v-if="dynamicValidateForm.names.length > 1"><i class="el-icon-circle-close"></i>移除</el-button></el-col>
    <el-col :span="2"><el-button type=text @click.prevent="addDomain(name)" v-if="index === (dynamicValidateForm.names.length-1)"><i class="el-icon-circle-plus-outline"></i>新增</el-button></el-col>
    </el-form-item>
    <!--提交表单=-->
    <el-form-item style="margin-left:21px">
      <el-button type="primary" @click="submitForm('dynamicValidateForm')">创建容器</el-button>
      <el-button @click="resetForm('dynamicValidateForm')">重置</el-button>
    </el-form-item>
</el-form>
</template>
<script>
  export default {
    data() {
      var judgeRepeat = async (rule, value, callback) => {
        if (value === '') {
          callback(new Error('容器名不能为空'));
        }else{
          let num = 1;
          for(let i = 0;i < this.dynamicValidateForm.names.length; i++){
            if(this.dynamicValidateForm.names[i].value === value){
              if(num >= 2)
              {
                callback(new Error('容器名重复'))
                return;
              }else{
                num ++
              }
            }
          }
          //接着判断容器名是否已经被使用过
          let containers = await this.$api.container.getContainers()
          for(var i = 0; i < containers.length; i++){
            let containerName = containers[i].Name[containers[i].Name.length-1].substring(1); //去掉第一个字符/
            if(value === containerName){
              callback(new Error('容器名已经被使用过'))
              return
            }
          }
          callback();
        }
      };
      return {
        dynamicValidateForm: {
          names: [{
            value: '',
          }],
        },
        append:{
          images:[{
            value:''
          }]
        },
        dialogTableVisible:false,
        dialogFormVisible:false,
        gridData: [],
        tableData:[],
        search:'',
        currentSelectItem:{},
        checked:null,
        currentPage:1,
        pageSizes:[3,5,10],
        pageSize:3,
        total:null,
        rules:{validator: judgeRepeat, trigger: 'blur'},
        settingform:{
          volume:'',
          network:'',
        },
        formLabelWidth: '120px',
        
      };
    },
    methods: {
      //初始化镜像列表
      async setImages(){
         this.gridData = await this.$api.image.getImages()
      },
      async createContainer(){
        let res = false
        for(let i=0;i<this.dynamicValidateForm.names.length;i++){
          let containerName = this.dynamicValidateForm.names[i].value
          let imageName = this.append.images[i].value
          res = await this.$api.container.createContainer(containerName, imageName)
          if(!res) {
            this.$message({type: 'error', message: '容器' + containerName + '创建失败'})
            break;
          }
        }
        this.$message({type: 'success', message: '创建成功'})
        return res
      },
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.$confirm('是否确认创建容器?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
            }).then(() => {
               this.createContainer()
            })
          } else {
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      removeDomain(item) {
        var index = this.dynamicValidateForm.names.indexOf(item)
        if (index !== -1) {
          this.dynamicValidateForm.names.splice(index, 1)
        }
      },
      addDomain() {
        this.dynamicValidateForm.names.push({
          value: '',
          key: Date.now()
        });
        this.append.images.push({
          value: ''
        });
      },

      handleSelectionChange (row) {
        if(row){
          this.currentSelectItem = row
        }
      },
      addImage(index){
        this.dialogTableVisible = false
        this.append.images[index].value = this.currentSelectItem.Tags[this.currentSelectItem.Tags.length -1]
      },
      // 初始页currentPage、初始每页数据数pagesize和数据data
      handleSizeChange: function (size) {
              this.pageSize = size;
      },
      handleCurrentChange: function(currentPage){
              this.currentPage = currentPage;
      },

    },
    computed:{
      searchData(){
        this.tableData=[] 
        this.gridData.forEach(item => {
          if(item.Tags[item.Tags.length -1 ].indexOf(this.search) != -1){
            this.tableData.push(item)
          }
        })
        if(this.tableData.length === 0){
          this.checked = false
          this.currentSelectItem = null
        }
        this.total = this.tableData.length
        return this.tableData;
      }
      

    },
    activated(){
      this.setImages()
    },
  }
</script>

<style scoped>
.demo-dynamic{
  margin-top :20px;
}
.el-radio__label {
  display: none;
}
.el-form-item__error{
  margin-left: 20px
}
</style>


