<template>
<div style="width:60%">
    <el-form label-position="left" label-width="150px" :model="form">
    <el-form-item label="容器ID">
        <el-input v-model="form.Id" readonly="true"></el-input>
    </el-form-item>
    <el-form-item label="创建时间">
        <el-input v-model="form.Created" readonly="true"></el-input>
    </el-form-item>
    <el-form-item label="镜像ID">
        <el-input v-model="form.Image" readonly="true"></el-input>
    </el-form-item>
    <el-form-item label="镜像ID">
        <el-input v-model="form.Image" readonly="true"></el-input>
    </el-form-item>
    <el-form-item label="工作目录">
        <el-input v-model="form.Config.WorkingDir" readonly="true"></el-input>
    </el-form-item>
    <el-form-item label="环境变量">
        <el-input v-model="form.Config.Env" readonly="true"></el-input>
    </el-form-item>
    <el-form-item label="HostnamePath">
        <el-input v-model="form.HostnamePath" readonly="true"></el-input>
    </el-form-item>
    </el-form>
</div>
</template>
<script>
export default {
    data(){
        return{
            form:{
                Id:'',
                created:'',

            }
        }
    },
    methods: {
      getTreeData(id){
          let url = "/api/containers/"+ id +"/json"
          this.$axios.get(url).then(res => {
              this.form = res.data
              console.log(res.data)
          })
      },
      handleNodeClick(data) {
        console.log(data);
      }
    },
    activated(){
      this.getTreeData(this.$route.query.id)
    },
}
</script>

<style scoped>
</style>


