<template>

<el-collapse accordion v-model="activeName">

  <el-collapse-item name="1">
    <template slot="title">
     <el-tag>选择节点<i class="el-icon-arrow-down"></i></el-tag>
    </template>
    <el-col :span="4" v-for="endpoint in endpoints" :key="endpoint.id" :offset="1">
        <el-card shadow="hover" >
          <el-row>
            <el-col>{{endpoint.ip}}:{{endpoint.port}}</el-col>
          </el-row>
          <el-row style="text-align:right">
            <el-col><el-button type="text" @click="selectEndpoint"><i class="el-icon-view"></i>查看</el-button></el-col>
          </el-row>
           
        </el-card>
    </el-col>
  </el-collapse-item>

  <el-collapse-item name="2">
    <template slot="title">
     <el-tag>新增节点<i class="el-icon-arrow-down"></i></el-tag>
    </template>
    <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
      <el-form-item label="IP" prop="ip">
        <el-input  v-model="ruleForm.ip" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="PORT" prop="port">
        <el-input  v-model="ruleForm.port" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')">提交</el-button>
        <el-button @click="resetForm('ruleForm')">重置</el-button>
      </el-form-item>
    </el-form>
  </el-collapse-item>
   
</el-collapse>

</template>

<script>
  export default {
    data() {
      var validatePort = (rule, value, callback) => {
         if (value === '') {
          callback(new Error('请输入端口'));
        } else {
          callback();
        }
      };
      var validateIp = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入IP'));
        } else {
          callback();
        }
      };
      return {
        activeName:["1"],
        endpoints:[],
        ruleForm: {
          ip: '',
          port: '',
        },
        rules: {
          ip: [
            { validator: validateIp, trigger: 'blur' }
          ],
          port: [
            { validator: validatePort, trigger: 'blur' }
          ]
        }
      };
    },
    methods: {
      //选择节点
      selectEndpoint(){

      },
      //获取节点列表
      async getEndpointList(){
        localStorage.setItem("username", "hu@qq.com")
        this.endpoints = await this.$api.endpoint.getEndpoints()
      },
      submitForm(formName) {
        this.$refs[formName].validate(async (valid) => {
          if (valid) {
            let endpoint = {
              ip: this.ruleForm.ip,
              port: this.ruleForm.port,
              email: localStorage.getItem("username") 
            }
            console.log("请求"+endpoint)
            let data = await this.$api.endpoint.addEndpoint(endpoint)

          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    },
    created(){
      this.getEndpointList()
    }
  }
</script>


