import http from './http'
export default {
//查询所有网络
getNetworks: async () => {
    let query = {
        filters : ''
    }
    let tableData = []
    await http.get("/api/networks").then(res =>{
        res.forEach(item =>{
            let resdata = {}
            resdata.Id = item.Id.substring(0,12)
            resdata.Scope = item.Scope
            resdata.Driver = item.Driver
            resdata.Name = item.Name
            tableData.push(resdata)
        })
    })
    return tableData
},
//查看网络详情
inspectNetwork: async  (id) => {
    let query = {
        verbose : '',
        scope: ''
    }
    this.tableData = {}
    let url = "/api/networks" + id
    await http.get(url).then(res =>{tableData = res})
    return tableData
},
//移除网络
removeNetwork: async  (id) => {
    let query = {
        verbose : '',
        scope: ''
    }
    let url = "/api/networks" + id
    let ret = false
    await http.delete(url).then(res =>{ret = true})
    return ret
},

//创建网络
createNetwork: async () => {
    let body ={
        "Name": "isolated_nw",
        "CheckDuplicate": false,
        "Driver": "bridge",
        "EnableIPv6": true,
        "IPAM": {
          "Driver": "default",
          "Config": [
            {
              "Subnet": "172.20.0.0/16",
              "IPRange": "172.20.10.0/24",
              "Gateway": "172.20.10.11"
            },
            {
              "Subnet": "2001:db8:abcd::/64",
              "Gateway": "2001:db8:abcd::1011"
            }
          ],
          "Options": {
            "foo": "bar"
          }
        },
        "Internal": true,
        "Attachable": false,
        "Ingress": false,
        "Options": {
          "com.docker.network.bridge.default_bridge": "true",
          "com.docker.network.bridge.enable_icc": "true",
          "com.docker.network.bridge.enable_ip_masquerade": "true",
          "com.docker.network.bridge.host_binding_ipv4": "0.0.0.0",
          "com.docker.network.bridge.name": "docker0",
          "com.docker.network.driver.mtu": "1500"
        },
        "Labels": {
          "com.example.some-label": "some-value",
          "com.example.some-other-label": "some-other-value"
        }
      }
    let url = "/api/networks/create"
    let ret = false
    await http.post(url, body).then(res =>{ret = true})
    return ret
},
//将容器连接到某个网路
connectContainerToNet: async (id) => {
    let body = {
        "Container": "3613f73ba0e4",
        "EndpointConfig": {
          "IPAMConfig": {
            "IPv4Address": "172.24.56.89",
            "IPv6Address": "2001:db8::5689"
          }
        }
    }
    let url = "/api/networks/" + id + '/connect'
    let ret = false
    await http.post(url, body).then(res =>{ret = true})
    return ret
},
//断开容器的网络连接
disconnectContainerToNet: async (id) => {
    let body = {
        "Container": "string",
        "Force": true
    }
    let url = "/api/networks/" + id + '/disconnect'
    let ret = false
    await http.post(url, body).then(res =>{ret = true})
    return ret
},

//删除不用的网络
deleteUnusednetworks: async () => {
    let ret = false
    await http.post("/api/networks/prune").then(res =>{ret = true})
    return ret
    //响应体
    let resbody = {
        "NetworksDeleted": [
          "string"
        ]
    }
},




}


