<template>
<el-form ref="form" :model="form" label-width="80px">
  <el-form-item label="Name">
    <el-input v-model="form.name"></el-input>
  </el-form-item>
  <el-form-item label="Driver">
    <el-input v-model="form.driver"></el-input>
  </el-form-item>
  <el-form-item label="Subnet">
    <el-input v-model="form.subnet"></el-input>
  </el-form-item>
  <el-form-item label="IP range">
    <el-input v-model="form.ipRange"></el-input>
  </el-form-item>
  <el-form-item label="Gateway">
    <el-input v-model="form.gateway"></el-input>
  </el-form-item>
  <el-form-item>
    <el-button type="primary" @click="onSubmit"><i class="el-icon-plus"></i> 创建网络</el-button>
  </el-form-item>
</el-form>
</template>

<script>
export default {
    data(){
        return{
            form:{
                name:'',
                driver:'',
                subnet:'',
                ipRange:'',
                gateWay:'',
            }
        }
    },
    methods: {
      onSubmit() {
        console.log('submit!');
      }
    }
}
</script>


