<template>
    <div style="text-align:left">
    
    <el-row>

    <el-dropdown split-button type="danger" size="small">
      <span> <i class="el-icon-delete" ></i> 移除</span>
    <el-dropdown-menu slot="dropdown">
    <el-dropdown-item>强制移除</el-dropdown-item>
    </el-dropdown-menu>
    </el-dropdown>

    <el-button type="primary" size="small" @click="buildImage"><i class="el-icon-plus"></i> 构建镜像</el-button>
      <el-button-group>
        <el-button type="primary" size="small"><i class="el-icon-upload2"></i> 上传镜像</el-button>
        <el-button type="primary" :disabled="export_status" size="small"><i class="el-icon-download"></i> 导出镜像</el-button>
      </el-button-group>

    </el-row>
    <el-row>
      <el-table
      :row-key="getRowKey"
      ref=multipleTable
      :data="tableData.slice((currentPage-1)*pageSize,currentPage*pageSize)"
      :v-model="tableData"
      stripe
      highlight-current-row
      style="width: 100%"
      tooltip-effect="dark"
      @selection-change="handleSelectionChange"
      >
        <el-table-column
          type="selection"
          width="55"  :reserve-selection="reserveSelection">
        </el-table-column>
         <el-table-column prop="Id" label="镜像ID" v-if="false"></el-table-column>
        <el-table-column prop="Tags" label="标签"></el-table-column>
        <el-table-column   prop="Size" label="状态"></el-table-column>
        <el-table-column  prop="Created" label="创建时间"></el-table-column>
      </el-table>
    </el-row>
    <el-row>
    <div class="block">
      <el-pagination
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="pageSizes"
        :page-size="pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="tableData.length">
      </el-pagination>
    </div>
    </el-row>
    
  </div>
</template>


<script>
  export default {
    
    data() {
      return {
        
        tableData:[],
        export_status:true,
        remove_status:true,
        images_Ids:[],
        currentPage:1,
        pageSizes:[3,5,10],
        pageSize:3,
        multipleSelection: [],
        reserveSelection:true
      }
    },
    methods: {
        async setTable(){
          this.loading = true
          this.tableData = await this.$api.image.getImages()
          this.loading = false
      },
      getRowKey(row){
        return row.id
      },
      handleSelectionChange(val){
        this.images_Ids = []
        val.forEach(item => {
          this.images_Ids.push(item.Id)
        })
        if(this.images_Ids.length !== 0){
            this.remove_status = this.export_status = false
        }else{
            this.remove_status = this.export_status = true
        }
      },
      // 初始页currentPage、初始每页数据数pagesize和数据data
        handleSizeChange: function (size) {
                this.pageSize = size;
        },
        handleCurrentChange: function(currentPage){
                this.currentPage = currentPage;
        },

        buildImage(){
          this.$router.push("/home/buildImage");
        }
    },
    activated(){
      this.setTable()
    },
   
  }
</script>

<style scoped>
.el-table-column{
  height: 8px;
}
 .el-row {
    margin-bottom: 20px;
  }
  .el-pagination{
    text-align: right;
  }
  .el-scrollbar__wrap{
    overflow: hidden;
  }
</style>


