<template>
<div>
    <el-row>
        <el-divider>选择环境类型</el-divider>
    </el-row>
    <el-row style="height:100px">
        <el-button @click="tab(1)" class="bottonSize" type="primary" >Agent</el-button>
        <el-button @click="tab(2)" class="bottonSize" type="primary">Edget Agent</el-button>
        <el-button @click="tab(3)" class="bottonSize" type="primary">Docker</el-button>
    </el-row>
    <el-row>
        <el-divider>设置环境变量</el-divider>
    </el-row>
    <el-row>
        <agentForm v-show="agentShow"></agentForm>
        <edageAgentForm v-show="edgetAgentShow"></edageAgentForm>
        <dockerForm v-show="dockerShow"></dockerForm>
    </el-row>
   


</div>
</template>
<script>
import AgentForm from './AgentForm'
import EdageAgentForm from './EdageagentForm'
import DockerForm from './DockerForm'
export default {
    data(){
        return {
            form:{
                name:'',
                url:'',
                publicIP:''
            },
            agentShow:false,
            edgetAgentShow:false,
            dockerShow:false,
            selectType:[false,true,true]
        }
    },
    components:{
       agentForm:AgentForm,
       edageAgentForm:EdageAgentForm,
       dockerForm:DockerForm
    },
    methods:{
        tab(index){
            this.agentShow = this.edgetAgentShow = this.dockerShow = false
            switch (index) {
                case 1:
                    this.agentShow = true
                    break;
                case 2:
                    this.edgetAgentShow = true
                    break;
                case 3:
                    this.dockerShow = true
                    break;
                default:
                    break;
            }
        }
    },
     activated(){
      this.agentShow = true
    },
}
</script>
<style scoped>
    .el-row{
        text-align: left;
    }
    .el-divider{
        margin-top: 8px;
    }
    .bottonSize{
        width: 100px;
        height: 90%;
    }
</style>


