import http from './http'
export default {
//登录
login: async (user) => {
    let data = [];
    await http.post('/users/login', user)
        .then(response => {data = response})
        .catch(() => {data = null});
    return data;
},
//注册
register: async (user) => {
    let data = []
    await http.post('/users', user)
        .then(response => {data = response})
        .catch(() => {data = null});
    return data;
},
}




