<template>
    <div>
    <el-row style="text-align:left">
      <el-button-group>
        <el-button type="success" :disabled="start_status" size="small" @click="containerOpt('start')"><i class="el-icon-video-play"></i> 启动</el-button>
        <el-button type="danger" :disabled="stop_status" size="small" @click="containerOpt('stop')"><i class="el-icon-video-pause"></i> 停止</el-button>
        <el-button type="primary" :disabled="restart_status" size="small" @click="containerOpt('restart')"><i class="el-icon-refresh"></i> 重启</el-button>
        <el-button type="primary" :disabled="pause_status" size="small" @click="containerOpt('pause')"><i class="el-icon-video-pause"></i> 暂停</el-button>
        <el-button type="primary" :disabled="resume_status" size="small" @click="containerOpt('unpause')"><i class="el-icon-video-play"></i> 恢复</el-button>
        <el-button type="danger" :disabled="remove_status" size="small" @click="containerOpt('remove')"><i class="el-icon-delete"></i> 移除</el-button>
      </el-button-group>
      <el-button style="margin-left: 10px" :disabled="create_status" type="primary" size="small" @click="createContainer" ><i class="el-icon-plus"></i> 创建容器</el-button>
    </el-row>
    <el-row>
      <el-table
      :row-key="getRowKey"
      ref=multipleTable
      v-loading="loading"
      :data="tableData.slice((currentPage-1)*pageSize,currentPage*pageSize)"
      :v-model="tableData"
      stripe
      highlight-current-row
      style="width: 100%"
      tooltip-effect="dark"
      @selection-change="handleSelectionChange"
      
      >
        <el-table-column
          type="selection"
          width="55"  :reserve-selection="reserveSelection">
        </el-table-column>
         <el-table-column prop="Id" label="容器ID" v-if="false"></el-table-column>
        <el-table-column prop="Name" label="名称"></el-table-column>
        <el-table-column   prop="State" label="状态">
          <template slot-scope="tableData">
            <el-tag v-if="tableData.row.State=='paused'" type="warning" size="small">已暂停</el-tag>
            <el-tag v-if="tableData.row.State=='exited'" type="danger" size="small">已停止</el-tag>
            <el-tag v-if="tableData.row.State=='running'" type="success" size="small">运行中</el-tag>
            <el-tag v-if="tableData.row.State=='created'" type="success" size="small">已创建</el-tag>
            <el-tooltip class="item" effect="dark" content="查看详情" placement="top-start">
              <el-link icon="el-icon-view" @click.prevent="inspectContainer(tableData.row)"></el-link>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="容器状态信息" placement="top-start">
               <el-link icon="el-icon-s-marketing" @click.prevent="containerState(tableData.row)"></el-link>
            </el-tooltip>
          </template>
        </el-table-column>
        <el-table-column  prop="Image" label="镜像"></el-table-column>
        <el-table-column prop="Created" label="创建时间"></el-table-column>
        <el-table-column prop="IPAddress" label="IP地址"></el-table-column>
      </el-table>
    </el-row>
    <el-row>
    <div class="block">
      <el-pagination
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="pageSizes"
        :page-size="pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="tableData.length"
        v-if="paginationShow"
        >
      </el-pagination>
    </div>
    </el-row>
  </div>
</template>


<script>
  export default {
    data() {
      return {
        tableData:[],
        multipleSelection: [],
        start_status:true,
        stop_status:true,
        restart_status:true,
        resume_status:true,
        remove_status:true,
        pause_status:true,
        containers_Ids:[],
        create_status:false,
        currentPage:1,
        pageSizes:[3,5,10],
        pageSize:3,
        loading:true,
        multipleSelection: [],
        paginationShow:true, 
        reserveSelection:true
      }
    },
    methods: {
      async setTable(){
        this.loading = true //加载数据前进行等待
        let tempData = await this.$api.container.getContainers()
        if(tempData !== null){
          this.tableData = tempData
        }else{
          this.$message({type:'error', message:'获取容器列表失败'})
        }
        this.loading = false
      },

      changeAllstatus(status){
        this.start_status = this.stop_status = this.restart_status = this.resume_status = this.remove_status = this.pause_status = status
      },

      changeButtonStatus(State){
        this.changeAllstatus(true)
        State.forEach(item =>{
          if(item == "exited"){ //停止
            this.start_status = this.restart_status = this.remove_status = false
          }else if(item == "running"){
            this.pause_status = this.stop_status = this.restart_status = this.remove_status = false
         }else if(item == "paused"){
            this.resume_status = this.stop_status = this.restart_status = this.remove_status = false 
         }else if(item == "created"){
           this.start_status = this.restart_status = this.remove_status = false
         }
        })
       
      },
      toggleSelection(rows) {
        if (rows) {
          rows.forEach(row => {
            this.$refs.multipleTable.toggleRowSelection(row);
          });
        } else {
          this.$refs.multipleTable.clearSelection();
        }
      },
      handleSelectionChange(val){
        this.multipleSelection = val;
        let status = []
        this.containers_Ids = []
        val.forEach(item => {
          status.push(item.State)
          this.containers_Ids.push(item.Id)
        })
        this.changeButtonStatus(status)
      },
      getRowKey(row){
        return row.Id 
      },

      // 初始页currentPage、初始每页数据数pagesize和数据data
      handleSizeChange: function (size) {
              this.pageSize = size;
      },
      handleCurrentChange: function(currentPage){
              this.currentPage = currentPage;
      },

      //发起容器操作请求
      async optQuery(opt){
        this.loading = true //表格状态显示在请求中
        //禁用分页
        this.paginationShow = false 
        //禁用所有按钮
        this.create_status= this.start_status = this.stop_status = this.restart_status = this.resume_status = this.remove_status = this.pause_status = true
        //发起操作请求
        let res = false
        for(let i=0 ;i < this.containers_Ids.length; i++){
           res = await this.$api.container.containerOpt(this.containers_Ids[i], opt)
           if(!res) break;
        }
        this.setTable() //操作请求处理完后重新查询容器
        //恢复分页状态
        this.paginationShow = true
        //清空已经选取的行
        this.multipleSelection = []
        this.$refs.multipleTable.clearSelection();
        //创建容器按钮状态恢复
        this.create_status = false 
        //返回操作结果
        return res
     },

     //判断选中的容器是否全部可以进行某项操作
     allowOpt(opt){
       switch (opt) {
         case "start":
           for(var i = 0; i< this.multipleSelection.length; i++){
             let item = this.multipleSelection[i]
             if(item.State != "exited" && item.State != "created" ){
                this.$message({type:'error', message:item.Name + '不是停止状态和创建状态无法启动'})
                return false;
              }
           }
           break;
       case "stop":
            for(var i = 0; i< this.multipleSelection.length; i++){
              let item = this.multipleSelection[i]
              if(item.State  != "running" ){
                this.$message({type:'error', message:item.Name + '不是运行状态无法停止'})
                return false;
              }
            }
           break;
        case "pause":
            for(var i = 0; i< this.multipleSelection.length; i++){
              let item = this.multipleSelection[i]
              if(item.State  != "running" ){
                this.$message({type:'error', message:item.Name + '不是运行状态无法暂停'})
                return false;
              }
            }
           break;
        case "unpause":
           for(var i = 0; i< this.multipleSelection.length; i++){
              let item = this.multipleSelection[i]
              if(item.State  != "paused" ){
                this.$message({type:'error', message:item.Name + '不是暂停状态无法恢复'})
                return false;
              }
            }
           break;
        case "restart":
            for(var i = 0; i< this.multipleSelection.length; i++){
              let item = this.multipleSelection[i]
              if(item.State  != "running" && item.State != "exited" && item.State != "created"){
                this.$message({type:'error', message:item.Name + '不是运行状态、停止状态以及创建状态无法重启'})
                return false;
              }
            }
           break;
        case "remove":
            for(var i = 0; i< this.multipleSelection.length; i++){
              let item = this.multipleSelection[i]
              if(item.State  === "running"){
                  return false;
              }
            }
            break;
         default:
           break;
       }
       return true
     },

     //所有容器操作的统一入口函数
     containerOpt(opt){
       if(opt === "remove"){
         let res = false
         let successInfo = "移除成功!"
         let errorInfo = "移除失败!"
         if(!this.allowOpt(opt)){ //操作如果不允许则提示是否强制删除
            this.$confirm('容器正在运行,是否强制删除?', '提示', {confirmButtonText: '确定', cancelButtonText: '取消',type: 'warning'})
                .then( async () => {
                  res = await this.optQuery(opt)
                  if(res){this.$message({type: 'success',message: successInfo});
                  }else{this.$message({type: 'error',message: errorInfo});}   
                }).catch(() => {this.$message({type:'info', message:"已取消操作"})});
         }else{
            this.$confirm('是否确认移除容器', '提示', {confirmButtonText: '确定', cancelButtonText: '取消',type: 'warning'})
                .then( async () => {
                  res = await this.optQuery(opt)
                  if(res){this.$message({type: 'success',message: successInfo});
                  }else{this.$message({type: 'error',message: errorInfo});}   
                  }).catch((error) => { console.log(error); this.$message({type:'info', message:"已取消操作"})});
         }
       }else if(this.allowOpt(opt)){ //确认操作是否可以执行
         let info = ""
         switch (opt) {
           case "start": info = "启动";break;
           case "restart": info = "重启";break;
           case "stop": info = "停止";break;
           case "pause": info = "暂停";break;
           case "unpause": info = "恢复";break;
           case "remove": info = "移除";break;
           default:
             break;
         }
         //弹框确认是否要进行该操作
        this.$confirm('是否确认' + info + '容器?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
          }).then( async () => {
            let res = await this.optQuery(opt)
            if(res){this.$message({type: 'success',message: info+'成功!'});
            }else{this.$message({type: 'error',message: info+'失败!'});}   
            
          }).catch((error) => {
            console.log(error)
            this.$message({ type: 'info',message: '已取消操作'});          
          });
      }
        
     },

      inspectContainer(row){
        this.$router.push({path:"/home/inspectContainer", query:{id:row.Id}})
      },
      containerState(row){
        if(row.State != "running")
        {
            this.$message({ type: 'info',message: '容器未运行，无法查看其运行时状态信息'}); 
        }else{
          this.$router.push({path:"/home/ContainerState", query:{id:row.Id}})
        }
      },
      createContainer(){
        this.$router.push("/home/createContainer")
      },
    },
    activated(){
      
      this.setTable()
    },
   
  }
</script>

<style scoped>
  .el-table-column{
    height: 8px;
  }
  .el-pagination{
    text-align: right;
  }
  .el-row {
    margin-bottom: 20px;
  }
  .item {
      margin: 4px;
  }
</style>


