import Vuex from 'vuex'
import Vue from 'vue' 
import persistedState from 'vuex-persistedstate'
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    token:null,
    isUserLogin:false,
    username:null,
  },
  getters:{//Getter接受state作为其第一个参数
    doneTools:state =>{
     return state.token//返回值会根据依赖被缓存起来
    },
    userLogin:state =>{
      return state.isUserLogin//返回值会根据依赖被缓存起来
    },
    userName: state => {
      return state.username //返回用户名
    }
  },
  mutations: {
    setToken(state, token){ //保留用户token信息
        state.token = token
        if(token){
            state.isUserLogin = true
        }else{
            state.isUserLogin = false
        }
    },
    setUsername(state, username){
      state.username = username
    }
  },
  actions: {
    setToken({commit}, token){  commit('setToken', token) },
    setUsername({commit}, username){  commit('setUsername', username) },

  },
  plugins: [persistedState()] //默认存储在localStorage 永久存储
})


